import React from "react";
import { FiCheckCircle, FiInfo, FiArrowRight } from "react-icons/fi";
import "../NominationCTA/ProcessNomination.css";
import Footer from "../footer/Footer";
import Header from "../header/Header";
import PopularTours from "../5t principle/principle";
import { Reveal } from "../reveal/Reveal";
import { useLanguage } from "../../context/LanguageContext";
import { getContent } from "../../content/content";

import "./nominate.css";
import { Link } from "gatsby";
import { getIcon } from "../../content/get-icon";
import { getContentKey } from "../../content/content";




const getSafeSrc = (val: any): string => {
  if (!val) return "";
  if (typeof val === 'string') return val;
  if (typeof val === 'object') {
    return val.src || val.background || val.image || val.bgImage || val.url || val.original || "";
  }
  return "";
};

const HeroSection: React.FC<{ content: any }> = ({ content }) => {
  const { processHero: hero } = content.pages["process-nomination"].section;

  return (
    <section className="proc-hero" aria-label="Process & Nomination Hero">
      <img
        src={getSafeSrc(hero.bgImage)}
        alt={hero.alt || ""}
        className="proc-hero__bg"
        loading="eager"
      />
      <div className="proc-hero__overlay" />
      <div className="proc-hero__inner">
        <div className="proc-hero__content">
          <p className="proc-hero__eyebrow">{hero.eyebrow}</p>
          <h1 className="proc-hero__title">
            <span className="proc-hero__title-main">{hero.titleMain}</span>
            <span className="proc-hero__title-sub">{hero.titleSub}</span>
          </h1>
          <p className="proc-hero__desc">{hero.description}</p>
          <div className="proc-hero__cta-row">
            <Link to="/nominate" className="thv-btn thv-btn--gold">
              {hero.ctaPrimary} <FiArrowRight aria-hidden="true" />
            </Link>
            <a href="#process-timeline" className="thv-btn thv-btn--ghost">
              {hero.ctaSecondary}
            </a>
          </div>
          <ul className="proc-hero__highlights">
            {hero.highlights.map((hl: any, i: number) => {
              const IconComponent = getIcon(hl.icon);
              return (
                <li key={i} className="proc-hero__highlight">
                  <span className="thv-ico">{IconComponent && <IconComponent />}</span>
                  <div>
                    <div className="thv-hl-title">{hl.title}</div>
                    <div className="thv-hl-detail">{hl.detail}</div>
                  </div>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    </section>
  );
};

const ProcessSection: React.FC<{ content: any }> = ({ content }) => {
  const {
    processSectionHeader: sectionHeader,
    processSteps: steps,
    processEligibility: eligibility,
    processCriteria: criteria,
    processLabels,
    processFeatures,
  } = content.pages["process-nomination"].section;

  return (
    <section
      className="proc-section"
      aria-label="Process & Nomination"
      id="process-timeline"
    >
      <Reveal direction="up" delay={200}>
        <div className="proc-header">
          <h2 className="proc-heading">{sectionHeader.title}</h2>
          <h3 className="proc-subline">{sectionHeader.subtitle}</h3>
          <p className="proc-intro">{sectionHeader.intro}</p>
        </div>
      </Reveal>
      <div className="proc-timeline">
        <PopularTours steps={steps} features={processFeatures} />
      </div>
      <div className="grid-wrap">
        <Reveal direction="left" delay={400} className="full-height">
          <div className="card light">
            <h4 className="card-title">{processLabels.eligibilityTitle}</h4>
            <ul className="list">
              {eligibility.map((e: any, i: number) => (
                <li key={i}>
                  <FiCheckCircle aria-hidden="true" />
                  <span>{e}</span>
                </li>
              ))}
            </ul>
            <p className="note">
              <FiInfo aria-hidden="true" /> {processLabels.submissionNote}
            </p>
          </div>
        </Reveal>
        <Reveal direction="right" delay={600} className="full-height">
          <div className="card light">
            <h4 className="card-title">{processLabels.criteriaTitle}</h4>
            <ul className="list">
              {criteria.map((c: any, i: number) => (
                <li key={i}>
                  <FiCheckCircle aria-hidden="true" />
                  <span>{c}</span>
                </li>
              ))}
            </ul>
            <a className="text-link" href="/framework">
              {processLabels.frameworkLink}
            </a>
          </div>
        </Reveal>
      </div>
    </section>
  );
};

const NominationFormSection: React.FC<{ content: any }> = ({ content }) => {
  const { processNominationForm: nominationForm } = content.pages["process-nomination"].section;

  return (
    <Reveal direction="up" delay={800}>
      <div className="form-wrap" id="nomination-form">
        <div className="form-bg" aria-hidden="true" />
        <form className="form-card" onSubmit={(e) => e.preventDefault()}>
          <h4 className="form-title">{nominationForm.formTitle}</h4>
          <p className="form-sub">{nominationForm.formSub}</p>
          <div className="form-grid">
            <div className="field floating">
              <input id="yourName" name="yourName" required placeholder=" " />
              <label htmlFor="yourName">{nominationForm.fields.yourName}</label>
            </div>
            <div className="field floating">
              <input
                id="yourEmail"
                name="yourEmail"
                type="email"
                required
                placeholder=" "
              />
              <label htmlFor="yourEmail">{nominationForm.fields.yourEmail}</label>
            </div>
            <div className="field floating">
              <input id="nomineeName" name="nomineeName" required placeholder=" " />
              <label htmlFor="nomineeName">
                {nominationForm.fields.nomineeName}
              </label>
            </div>
            <div className="field floating">
              <select id="category" name="category" required defaultValue="">
                <option value="" disabled hidden />
                {nominationForm.fields.categoryOptions.map((opt: any, i: number) => (
                  <option key={i}>{opt}</option>
                ))}
              </select>
              <label htmlFor="category">{nominationForm.fields.category}</label>
            </div>
            <div className="field span-2">
              <label className="field-label">
                {nominationForm.fields.nomineeType}
              </label>
              <div className="pill-group">
                {nominationForm.fields.nomineeTypeOptions.map((opt: any, i: number) => (
                  <label key={i} className="pill">
                    <input
                      type="radio"
                      name="type"
                      value={opt}
                      required={i === 0}
                    />
                    <span>{opt}</span>
                  </label>
                ))}
              </div>
            </div>
            <div className="field span-2 floating">
              <textarea
                id="why"
                name="why"
                required
                rows={5}
                placeholder=" "
              />
              <label htmlFor="why">{nominationForm.fields.why}</label>
            </div>
          </div>
          <div className="form-actions">
            <button className="btn-gold" type="submit">
              {nominationForm.submitButton}
            </button>
          </div>
          <p className="form-note">{nominationForm.formNote}</p>
        </form>
      </div>
    </Reveal>
  );
};


const ProcessNomination: React.FC = () => {
  const { lang } = useLanguage();
  const [isAdminPreview, setIsAdminPreview] = React.useState(false);
  const [adminData, setAdminData] = React.useState<any>(null);

  // 1. Storage Sync Logic
  React.useEffect(() => {
    if (typeof window === "undefined") return;

    const sync = () => {
      try {
        const raw = localStorage.getItem("admin");
        if (raw) {
          const parsed = JSON.parse(raw);
          const hasContent = parsed?.contentEN || parsed?.contentVN || parsed?.CONTENT?.contentEN;
          if (parsed && hasContent) {
            setAdminData(parsed);
            setIsAdminPreview(true);
            return;
          }
        }
      } catch (e) { }
      setAdminData(null);
      setIsAdminPreview(false);
    };

    sync();
    window.addEventListener("storage", sync);
    return () => window.removeEventListener("storage", sync);
  }, []);

  // 2. Content Resolution
  const langKey = getContentKey(lang);
  let content = getContent(lang);

  if (isAdminPreview && adminData) {
    const root = adminData.CONTENT || adminData;
    if (root[langKey]) {
      content = root[langKey];
    } else if (root.pages) {
      content = root;
    }
  }

  // Safety fallback
  if (!content?.pages?.["process-nomination"]) {
    content = getContent(lang);
  }

  return (
    <div style={{ background: "#1A202C", minHeight: "100vh" }}>
      {isAdminPreview && (
        <div style={{ height: 48, width: "100%", backgroundColor: "#2d3748", borderBottom: "3px solid #ecc94b", position: "fixed", top: 0, left: 0, zIndex: 999999, display: "flex", alignItems: "center", justifyContent: "space-between", padding: "0 24px", color: "#fff", fontFamily: 'Inter, sans-serif' }}>
          <span style={{ fontWeight: 800, color: '#ecc94b', fontSize: 13 }}>üõ†Ô∏è DRAFT PREVIEW ACTIVE</span>
          <button onClick={() => { localStorage.removeItem("admin"); window.location.reload(); }} style={{ background: '#e53e3e', border: 'none', color: '#fff', padding: '4px 12px', borderRadius: 4, cursor: 'pointer', fontSize: 11, fontWeight: 700 }}>Exit Preview</button>
        </div>
      )}
      <div style={{ paddingTop: isAdminPreview ? 48 : 0 }}>
        <Header content={content?.pages?.globals?.section?.headerContent} />
        <HeroSection content={content} />
        <ProcessSection content={content} />
        <Footer content={content?.pages?.globals?.section?.footerContent} />
      </div>
    </div>
  );
};

export default ProcessNomination;
